import { Component, OnInit, ViewChild } from '@angular/core';
import { LeadService } from '../lead/lead.service';
import { Login } from '../login/login.component'

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
})

export class AppComponent implements OnInit{
  // Lead Container
  leadsData: any[] = [];
  token: string;
  filters: string = '?';

  @ViewChild(Login) login: Login;

  toggleLeads(show_leads: boolean):void {
    if (show_leads) {
      this.getLeads();
    } else {
      this.leadsData = [];
    }
  }

  constructor(private leadService: LeadService) {
  }

  urlParam(name){
    var results = new RegExp('[\?&]' + name + '=([^&#]*)').exec(window.location.href);

    if (results != null){
      return results[1] || 0;
    } else{
      return null;
    }
  }

  getLeads(){
    var that = this;
    console.log("Getting Leads...")
    this.leadService.getLeads(this.filters).subscribe(
      data => this.populateLeads(data.results),
      err => console.error(err),
      () => {
        this.login.showLogin(false);
        this.login.showLogout(true);
      }
    )
  }

  populateLeads(leads_data){
    for (var i = 0; i < leads_data.length; ++i) {
      this.leadsData.push(leads_data[i]);
    }
    var res = this.login.showLoader(false);
  }

  ngOnInit(){
    this.login.showLogin(true);
    this.login.showLogout(false);
    this.login.showLoader(false);
    this.login.deactivateForm();
    var currentToken = JSON.parse(localStorage.getItem('current_token'));
    var refresh_token = JSON.parse(localStorage.getItem('refresh_token'));

    if (this.urlParam('postal_code') != null){
      this.filters += 'postal_code=' + this.urlParam('postal_code');
    }
    if (refresh_token != null){
      this.login.showLoader(true)
      this.login.setRefreshToken(refresh_token['token'])
      this.login.refreshToken()
    } else {
      this.login.activateForm();
      this.login.showLoader(false);
    }
  }
}



