import {Component, Input, Output, EventEmitter} from '@angular/core';
import {LoginService} from './login.service';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import {LeadService} from "../lead/lead.service";
declare var $: any;

@Component({
  selector: 'login',
  templateUrl: 'login.component.html',
})

export class Login {

  @Output() toggleLeads: EventEmitter<boolean> = new EventEmitter<boolean>();

  private token: string;
  private refresh_token: string;
  private username: string = '';
  private password: string = '';

  constructor(public loginService: LoginService, public leadService: LeadService) {
  }

  _setToken(data) {
    console.log("Setting Token...")
    this.token = data['access_token'];
    this.refresh_token = data['refresh_token']

    localStorage.setItem('current_token', JSON.stringify({token: this.token}));
    localStorage.setItem('refresh_token', JSON.stringify({token: this.refresh_token}));

    this.leadService.setToken(data['access_token'])
  }

  setRefreshToken(token) {
    console.log(token)
    this.refresh_token = token;
  }

  _getToken(){
    return this.token
  }

  delToken(){
    this.token = null;
    this.refresh_token = null;
    localStorage.removeItem('current_token');
    localStorage.removeItem('refresh_token');
    this.leadService.setToken(null);
    return this.token
  }

  showLogin(make_visible) {
    if (make_visible){
      this.activateForm();
      $('#login_form').css('display', 'block');
    } else {
      this.deactivateForm();
      $('#login_form').css('display', 'none');
    }
  }

  showLogout(make_visible) {
    if (make_visible){
      $('#logout').css('display', 'block');
    } else {
      $('#logout').css('display', 'none');
    }
  }

  showLoader(make_visible) {
    if (make_visible){
      $('#login_loader').css('display', 'block');
    } else {
      $('#login_loader').css('display', 'none');
    }
  }

  deactivateForm() {
    $('#submit_button').addClass('disabled');
    $('#username').prop('disabled', true);
    $('#password').prop('disabled', true);
  }

  activateForm() {
    $('#submit_button').removeClass('disabled');
    $('#username').prop('disabled', false)
    $('#password').prop('disabled', false)
  }

  setToken() {
    var that = this;
    this.loginService.login(this.username, this.password)
      .catch(err => {
        this.activateForm();
        return Observable.throw(err);
      })
      .map(response => response.json())
      .subscribe(
        data => this._setToken(data),
        err => function () {
          this.activateForm();
          this.showLoader(false);
          console.error(err);
        },
        {} = function(){
          that.showLogin(false);
          that.toggleLeads.emit(true);
        }
      )
  }

  refreshToken() {
    var that = this;
    this.loginService.refresh(this.refresh_token)
      .catch(err => {
        this.activateForm();
        return Observable.throw(err);
      })
      .map(response => response.json())
      .subscribe(
        data => this._setToken(data),
        err => function () {
          this.activateForm();
          this.showLoader(false);
          console.error(err);
        },
        {} = function(){
          that.showLogin(false);
          that.toggleLeads.emit(true);
        }
      )
  }

  loseToken() {
    this.loginService.logout(this.token)
      .catch(err => {
        console.log("Error trying to revoke token on server.");
        return Observable.throw(err);
      })
      .map(response => response.json())
      .subscribe(
        data => data,
        err => function () {
          console.error(err);
        },
      );
  }

  login(){
    console.log("Logging in...")
    this.showLoader(true)
    this.deactivateForm()
    this.setToken();
  }

  logout(){
    console.log("Logging out...")
    this.loseToken();
    this.delToken();
    console.log("Removing leads...")
    this.toggleLeads.emit(false);
    this.showLogin(true);
    this.showLogout(false);
  }

}



